import fetch from "../utils/fetch"
// 卡号支付
export function cardPay(data) {
    return fetch({
        url: "/pay/front/cardPay",
        method: "post",
        data
    })
}
// 通过订单号查询激活码信息
export function getInfoByOrderNo(data) {
    return fetch({
        url: "/pay/front/"+ data.orderId,
        method: "get",
    })
}
// 获取订单号
export function getOrderId(data) {
    return fetch({
        url: "/wxPay/front/getOrderId",
        method: "post",
        data
    })
}

// 请求微信支付
export function requestWxPay(data) {
    return fetch({
        url: `/wxPay/front/pay`,
        method: "post",
        data
    })
}

