// 轮询订单状态
export function getOrderStatus(data) {
    return fetch({
        url: `/wxPay/front/getDetail/${data.orderId}
        `,
        method: "get",
    })
}
// 支付宝支付
export function aliPay(data) {
    return fetch({
        url: `/Alipay/front/createNative`,
        method: "post",
        data
    })
}
// 轮询获取支付宝支付状态
export function getAliOrderStatus(data) {
    return fetch({
        url: `/Alipay/front/queryPayStatus`,
        method: "get",
        params: data
    })
}

